.comments-container {
  padding-top: 80px;
  padding-bottom: 50px;
  //  padding-bottom: 86px;
  user-select: none;

  @media screen and(min-width: $tablet) and (max-width: 1199px) {
    // max-width: 620px;
    padding-top: 121px;
    // padding-bottom: 119px;
    padding-right: 0;
    padding-left: 0;
  }
  @media screen and(min-width: $desktop) {
    // max-width: 926px;
    padding-top: 121px;
    // padding-bottom: 119px;
    padding-right: 0;
    padding-left: 0;
  }
}
.comments-slider {
  margin-left: auto;
  margin-right: auto;
  overflow-x: hidden;

  @media screen and (max-width: 767px) {
    width: 100%;
  }
  @media screen and (min-width: $tablet) and (max-width: 1199px) {
    width: 620px;
  }

  @media screen and(min-width: $desktop) {
    width: 926px;
  }
}

.comment-slider-item {
  outline: 1px solid transparent;
  display: inline-block;
}

.comments-slick-dots {
  display: block;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
  margin: 0;
}

.comments-slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.comments-slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.comments-slick-dots li button:hover,
.comments-slick-dots li button:focus {
  outline: none;
}

.comments-slick-dots li button:hover:before,
.comments-slick-dots li button:focus:before {
  opacity: 1;
}

.comments-slick-dots li button:before {
  font-family: 'slick';
  font-size: 6px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 12px;
  height: 12px;
  content: '';
  background: url('../images/svg/circle-home.svg');
  background-repeat: no-repeat;
}

.comments-slick-dots li.slick-active button:before {
  width: 13px;
  height: 12px;
  background: url('../images/svg/home.svg');
  background-repeat: no-repeat;
  background-size: contain;
}

.comments-foto {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 85px;
  height: 85px;
  border-radius: 50%;
}

.comment-text-container {
  position: relative;
  z-index: -5;
  background-color: $alternate-bg-color;

  @media screen and (max-width: 767px) {
    margin-top: 29px;
    margin-bottom: 30px;
  }
  @media screen and (min-width: $tablet) and (max-width: 1199px) {
    margin-top: 18px;
    margin-bottom: 24px;
  }

  @media screen and (min-width: $desktop) {
    margin-top: 15px;
    margin-bottom: 15px;
  }
}

.comment-text {
  font-family: $primary-font;
  font-weight: 500;
  text-align: center;
  line-height: 2.58;
  letter-spacing: 0.04em;
  color: $black;

  @media screen and (max-width: 767px) {
    padding: 41px 50px 20px 50px;
    font-size: 12px;
    line-height: 2;
  }

  @media screen and (min-width: $tablet) and (max-width: 1199px) {
    // padding: 47px 3px 28px 0px;
    padding-top: 47px;
    padding-bottom: 28px;
    padding-right: 36px;
    padding-left: 60px;
    font-size: 14px;
    line-height: 2;
    // width: 544px;
  }

  @media screen and (min-width: $desktop) {
    padding: 47px 89px 29px 67px;

    font-size: 16px;
    line-height: 1.875;
  }
}

.comment-text-tablet {
  width: 544px;
}

.comment-text-desktop {
  width: 813px;
}

.comment-dekor {
  position: absolute;
  display: block;
  top: 28px;
  left: 61px;

  z-index: -1;
  width: 54px;
  height: 40px;

  background-image: url('../images/svg/slick-bg.svg');

  fill: $primary-decor-color;
  background-repeat: no-repeat;
  background-size: contain;

  @media screen and (min-width: $tablet) and (max-width: 1199px) {
    top: 20%;
    left: 8%;
  }

  @media screen and(min-width: $desktop) {
    top: 22%;
    // left: 50px;
  }
}

@media screen and (max-width: 767px) {
  .comment-text-desktop,
  .comment-text-tablet {
    display: none;
  }
}

@media screen and (min-width: $desktop) {
  .comment-text-mobile,
  .comment-text-tablet {
    display: none;
  }
}

@media screen and (min-width: $tablet) and (max-width: 1199px) {
  .comment-text-mobile,
  .comment-text-desktop {
    display: none;
  }
}

.comment-dekorline {
  display: block;

  border-bottom: 1px solid $primary-decor-color;
  max-width: 180px;

  @media screen and (min-width: $tablet) and (max-width: 1199px) {
    max-width: 524px;
  }
  @media screen and (min-width: $desktop) {
    max-width: 813px;
  }
}

.comment-dots {
  display: flex;
  justify-content: center;

  & .dot {
    width: 4px;
    height: 4px;
    border-radius: 50%;
    background-color: $smallcircle-decor-color;
  }
  & .dot + .dot {
    margin-left: 10px;
  }
}

