.comment-local {
  margin-top: 15px;
  margin-bottom: 30px;
  font-family: $primary-font;
  font-weight: 500;

  line-height: 1.85;
  text-align: center;
  letter-spacing: 0.04em;
  font-size: 14px;

  @media screen and (min-width: $tablet) and (max-width: 1199px) {
    margin-top: 14px;
    margin-bottom: 35px;
    font-size: 16px;
    line-height: 1.875;
  }
  @media screen and(min-width: $desktop) {
    margin-top: 12px;
    margin-bottom: 32px;
    font-size: 20px;
  }
}

.zaglushka-foto {
  margin-left: auto;
  margin-right: auto;
  width: 85px;
  height: 85px;
  border-radius: 50%;
  background-color: crimson;
}
.zaglushka-home {
  width: 12px;
  height: 12px;
  border-radius: 50%;
  background-color: crimson;
}

